@media screen and (max-width: 576px) {
    .policy-none {
        display: none;
    }
  

    .box-footer-bottom {
        padding-top: 21px;
        height: 112px;
        padding-left: 16px;
        padding-bottom: 33px;
        display: flex;
        flex-direction: column; 
        align-items: flex-start;
    }
